<template>
  <div id="crew-monitor-info-fullscreen">
    <el-row type="flex" justify="center" align="middle" class="title-box">
      <el-row class="title-text">机组全屏监控</el-row>
      <el-row class="time-box"
        >当前时间：
        <span>{{ nowTime }}</span>
      </el-row>
    </el-row>
    <el-row class="content-box">
      <el-col :span="4">
        <el-card :shadow="shadow">
          <div slot="header" class="clearfix">
            <span>监控设置</span>
          </div>
          <el-row type="flex" align="middle">
            <el-col :span="12">
              <el-row type="flex" justify="start" align="middle">
                滚动：<el-switch v-model="isScroll"></el-switch>
              </el-row>
            </el-col>
            <el-col :span="12">
              <el-row type="flex" justify="end" align="middle">
                间隔：<el-select v-model="interval">
                  <el-option
                    v-for="item in intervalOpt"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
              </el-row>
            </el-col>
          </el-row>
        </el-card>
        <el-card :shadow="shadow">
          <div slot="header" class="clearfix">
            <span>状态</span>
          </div>
          <el-row type="flex" justify="center">
            <div v-for="(item, index) in mchState" :key="index">
              <state-box :color="item.color" :stop="index" />
              {{ item.label }}
            </div>
          </el-row>
        </el-card>
        <el-card :shadow="shadow">
          <el-input placeholder="请输入发电机组名称"></el-input>
          <div class="query-btn">搜索</div>
        </el-card>
        <el-card :shadow="shadow">
          <div slot="header">
            <span>机组列表({{ mchTotal }})</span>
            <svg-icon title="编辑" @click="dialogVisible = true" icon-class="edit"></svg-icon>
          </div>
          <el-row v-for="(item, index) in mchInfo" :key="index" class="item-box">
            <state-box :stop="item.state" :color="mchState[item.state].color" />
            <div class="name-text">{{ item.name }}</div>
            <signal-strength :strength="item.signal" :width="signal[0]" :height="signal[1]" />
          </el-row>
        </el-card>
      </el-col>
      <el-col :span="14">
        <crew-monitor-info class="crew-monitor-info" />
      </el-col>
      <el-col :span="6">
        <el-card class="map-box" :shadow="shadow">
          <div slot="header" class="clearfix">
            <span>机组定位</span>
          </div>
          <div
            id="fullscreen-map-box"
            v-loading="loading"
            :element-loading-text="loadingMsg"
            element-loading-background="rgba(0, 0, 0, 0.6)"
          ></div>
        </el-card>
        <el-card class="curve-box" :shadow="shadow">
          <div slot="header" class="clearfix">
            <span>燃油曲线</span>
          </div>
          <curve :id="'fullscreen-curve'" />
        </el-card>
      </el-col>
    </el-row>
    <!-- :title="`监控设置（全部：${mchTotal}）`" -->
    <el-dialog :visible.sync="dialogVisible" width="50%" :show-close="false">
      <span slot="title" class="dialog-title">
        <el-row>
          <el-col :span="12">
            <el-row type="flex" justify="start">监控设置（全部：{{ mchTotal }}）</el-row></el-col
          >
          <el-col :span="12">
            <el-row type="flex" justify="end" class="add"><div @click="_add">添加</div></el-row>
          </el-col>
        </el-row>
      </span>
      <TableList
        :options="options"
        :columns="columns"
        :dataSource="dataSource"
        :operates="operates(this)"
        :dataTotal="dataTotal"
        :pagination="paginationCfg"
        @handleChangePage="handleChangePage"
        @handleChangeSize="handleChangeSize"
      />
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Vue from 'vue'

import CrewMonitorInfo from '../index.vue'
import SignalStrength from '@/components/SignalStrength'
import loadBMap from '@/utils/loadBMap'
import { formatTime } from '@/utils/common.js'
import Curve from './Curve.vue'
import _data from './data'
import { paginationConfigMixin } from '@/mixins/paginationConfigMixin'

// 机组状态方框组件
Vue.component('StateBox', {
  props: ['color', 'stop'],
  template: `<div><div v-show="stop!=3"
  :style="{
      background:color,
      height:'20px',
      width:'20px',
      }">
      </div>
      <div v-show="stop===3"
  :style="{
      background:color,
      height:'20px',
      width:'20px',
      border:'2px solid black',
      boxSizing:'border-box'
      }">
      </div></div>
      `
})
const operates = (t) => [
  {
    label: '编辑',
    method: (row) => {
      t.handleNewJump(row)
    }
  }
]

export default {
  name: 'CrewMonitorInfoFullscreen',
  mixins: [paginationConfigMixin],
  components: {
    CrewMonitorInfo,
    SignalStrength,
    Curve
  },
  data() {
    return {
      nowTime: '',
      // el-card shadow 效果
      shadow: 'hover',
      // 机组列表总数
      mchTotal: 100,
      // 是否滚动
      isScroll: false,
      // 间隔
      interval: '1',
      // 间隔选项
      intervalOpt: [
        {
          label: '30秒',
          value: '1'
        },
        {
          label: '1分钟',
          value: '2'
        },
        {
          label: '3分钟',
          value: '3'
        },
        {
          label: '5分钟',
          value: '4'
        }
      ],
      // 机组状态
      // 0 报警 #ff0000 1 警告 #ffd800
      // 2 运行 #5cf548 3 停机 #ffffff
      // 4 离线 #808080
      mchState: [
        { color: '#ff0000', label: '报警' },
        { color: '#ffd800', label: '警告' },
        { color: '#5cf548', label: '运行' },
        { color: '#ffffff', label: '停机' },
        { color: '#808080', label: '离线' }
      ],
      // 机组列表信号值宽高
      signal: [25, 20],
      // 机组列表信息
      mchInfo: [],
      // 百度地图实例
      bMap: null,
      loading: false,
      loadingMsg: '地图加载中，请您稍候...',
      // 监控设置编辑界面
      dialogVisible: false,
      columns: [
        {
          label: '机组名称',
          prop: 'name',
          minWidth: '60%'
        }
      ],
      data: [],
      dataSource: [],
      options: {
        index: true,
        labelIndex: '序号',
        props: {
          // 操作列宽度
          width: 120
        }
      },
      operates
    }
  },
  methods: {
    queryTableData() {
      const arr = this.data.slice(
        (this.paginationCfg.currentPage - 1) * this.paginationCfg.pageSize,
        this.paginationCfg.currentPage * this.paginationCfg.pageSize
      )
      this.$set(this, 'dataSource', arr)
    },
    getNowTime() {
      setTimeout(() => {
        this.nowTime = this.$moment().format('yyyy-MM-DD HH:mm:ss')
        this.getNowTime()
      }, 1000)
    },
    // 渲染机组轨迹地图
    renderBMap() {
      this.loadingMsg = '地图加载中，请您稍候...'
      this.loading = true
      loadBMap()
        .then((e) => {
          this.$api.crewLocation
            .findAllGeneratroSetOrbitRecordV2(
              {
                model: {
                  StartTime: '2022-01-01',
                  EndTime: '2022-02-15 13:13',
                  Title: '暂无机组轨迹'
                }
              },
              {
                generatorSetId: 7297
                // generatorSetId: this.selectCrew.ID
              }
            )
            .then((res) => {
              this.loading = false
              // res = data.track
              this.dataObj = res.Data
              const BMap = e || undefined
              // Size
              // 此处百度地图设置的 size 必须和 icon 图标的大小一致，百度地图只是绘制了一个 size 大小的容器，然后将图标原封不动得放进去而已
              const size = new BMap.Size(48, 48)
              const map = new BMap.Map(`fullscreen-map-box`, {
                enableMapClick: false
              })
              const dataObj = this.dataObj
              const GensetMap = dataObj.GensetMap
              const OrbitList = dataObj.OrbitList
              const [x, y] = GensetMap ? [GensetMap.Longitude, GensetMap.Latitude] : []
              // 当前定位
              const centerPoint = new BMap.Point(x, y)
              this.centerPoint = centerPoint
              const marker = new BMap.Marker(centerPoint)
              const opts = {
                width: 300,
                height: 100,
                title: `<div style='font-size:1.2rem;font-weight:600'>${GensetMap.DisplayName}</div>`,
                offset: new BMap.Size(0, -10)
              }
              const infoWindow = new BMap.InfoWindow(
                `<el-card class="box-card">
                    <div  class="text item">
                        经度：${x}
                    </div>
                    <div  class="text item">
                        纬度：${y}
                    </div>
                    <div  class="text item">
                        ${GensetMap.InstallationSite}
                    </div>
                  </el-card>
                  `,
                opts
              )
              this.infoWindow = infoWindow
              marker.addEventListener(`click`, (_) => {
                map.openInfoWindow(infoWindow, centerPoint)
              })
              map.openInfoWindow(infoWindow, centerPoint)

              map.centerAndZoom(centerPoint, 16)
              map.addOverlay(marker)
              map.enableScrollWheelZoom()
              if (OrbitList && OrbitList.length > 0) {
                const polylineList = []
                OrbitList.forEach((e, index) => {
                  const [x, y] = [e.Longitude, e.Latitude]
                  const point = new BMap.Point(x, y)
                  const opts = {
                    width: 300,
                    height: 100,
                    title: `<div style='font-size:1.2rem;font-weight:600'>${formatTime(
                      e.AcquisitionTime
                    ).format(`YYYY年M月D日 HH:mm:ss`)}</div>`,
                    offset: new BMap.Size(0, 0)
                  }
                  const infoWindow = new BMap.InfoWindow(
                    `<el-card class="box-card">
                    <div  class="text item">
                        经度：${x}
                    </div>
                    <div  class="text item">
                        纬度：${y}
                    </div>
                  </el-card>
                  `,
                    opts
                  )
                  if (index === 0) {
                    // 开始图标
                    const iconBegin = new BMap.Icon(
                      require('@/assets/img/crewLocation/beginIcon48.png'),
                      size
                    )
                    const markerBegin = new BMap.Marker(point, {
                      icon: iconBegin
                    })
                    markerBegin.addEventListener('click', function () {
                      map.openInfoWindow(infoWindow, point) // 开启信息窗口
                    })
                    map.addOverlay(markerBegin)
                  } else if (index === OrbitList.length - 1) {
                    // 结束图标
                    const iconEnd = new BMap.Icon(
                      require('@/assets/img/crewLocation/endIcon48.png'),
                      size
                    )
                    const markerEnd = new BMap.Marker(point, {
                      icon: iconEnd
                    })
                    markerEnd.addEventListener('click', function () {
                      map.openInfoWindow(infoWindow, point) // 开启信息窗口
                    })
                    map.addOverlay(markerEnd)
                  } else {
                    // 中间点的图标
                    const icon = new BMap.Icon(
                      require('@/assets/img/crewLocation/locationIcon.png'),
                      new BMap.Size(16, 26)
                      // {anchor: new BMap.Size(-2, 0)}
                    )
                    const marker = new BMap.Marker(point, { icon: icon })
                    marker.addEventListener(`click`, () => {
                      map.openInfoWindow(infoWindow, point)
                    })
                    map.addOverlay(marker)
                  }
                  polylineList.push(point)
                })
                const polyline = new BMap.Polyline(polylineList, {
                  strokeColor: '#1ecfef',
                  strokeWeight: 2,
                  strokeOpacity: 0.5,
                  strokeStyle: `solid`,
                  enableEditing: false
                }) // 创建折线
                map.addOverlay(polyline) // 增加折线
                map.addEventListener(`movestart`, (_) => {
                  this.backBtnShow = true
                })
                this.bMap = map
              }
            })
        })
        .catch((err) => {
          alert(err)
        })
    },
    handleClick(row) {},
    // 分页 当前页改变
    handleChangePage(val) {
      this.paginationCfg.currentPage = val
      this.queryTableData()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      this.paginationCfg.currentPage = 1
      this.paginationCfg.pageSize = val
      this.queryTableData()
    },
    handleNewJump(row) {},
    getDataSource() {
      const arr = []
      _data.forEach((item) => {
        const obj = {
          name: item.DisplayName
        }
        arr.push(obj)
      })
      this.data = arr
    },
    _add() {
      this.$message('添加')
    }
  },
  mounted() {
    this.getNowTime()
    this.renderBMap()
    this.getDataSource()
    this.dataTotal = this.data.length
    this.queryTableData()
    const mchInfo = []
    for (let i = 0; i < 100; i++) {
      const obj = {
        signal: Number((Math.random() * 6).toFixed(0)),
        state: Number((Math.random() * 4).toFixed(0)),
        name: 'FPSS' + (Math.random() * (99999999 - 66666666) + 66666666).toFixed(0)
      }
      mchInfo.push(obj)
    }
    this.mchInfo = mchInfo
  }
}
</script>

<style lang="less" scoped>
#crew-monitor-info-fullscreen {
  height: 100%;
  padding: 0 10px 10px;
  box-sizing: border-box;
  /deep/.el-dialog {
    .add > div {
      cursor: pointer;
      user-select: none;
    }
  }
}
// 隐藏组件不需要显示的内容
/deep/.el-container {
  height: 100%;
  padding: 0 !important;
  overflow: inherit !important;
  .el-main {
    // padding: 0;
    overflow: inherit;
  }
  .el-aside {
    display: none;
  }
}
/deep/.main-top {
  display: none;
}
/deep/.crew-monitor-top2 {
  display: none;
}
/deep/.crew-monitor-top1 {
  display: none;
}
/deep/.crew-monitor-middle2 {
  // margin-left: 0 !important;
  // padding-left: 10px !important;
}
/deep/ .crew-monitor-keep {
  margin-left: 0 !important;
  padding-left: 10px !important;
}
// 卡片内容去掉内边距
/deep/.el-card__body {
  padding: 0;
  height: 260px;
}
// 卡片标题样式
/deep/.el-card__header {
  padding: 10px;
  background: #f3f3f3;
  text-align: center;
  font-size: 1.1rem;
  color: #888888;
}

.title-box {
  .title-text {
    font-size: 2rem;
  }
  .time-box {
    position: absolute;
    right: 10px;
    font-size: 1.2rem;
  }
}
.content-box {
  height: calc(100% - 38px);
  > .el-col {
    height: 100%;
  }
  > .el-col:nth-child(1) {
    display: flex;
    flex-direction: column;
    > .el-card:nth-child(n + 2) {
      margin: 12px 0 0 0;
    }
    > .el-card:nth-child(1) /deep/ .el-card__body {
      padding: 6px;
      height: 35px;
      white-space: nowrap;
    }
    > .el-card:nth-child(2) {
      .el-row {
        > div {
          display: flex;
          flex-direction: column;
          align-items: center;
          font-size: 0.8rem;
          margin: 0 10px;
        }
      }
      /deep/.el-card__body {
        height: 45px;
        padding: 10px 0 0 0;
      }
    }
    > .el-card:nth-child(3) {
      padding: 0 0 4px 0;
      /deep/ .el-card__body {
        display: flex;
        height: 32px;
        height: 40px;
        align-items: center;
        .el-input__inner {
          border-start-end-radius: 0;
          border-end-end-radius: 0;
        }
        .query-btn {
          height: 32px;
          width: 70px;
          background: #46ae5a;
          color: #fff;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          user-select: none;
        }
      }
    }
    > .el-card:nth-child(4) {
      flex: 1;
      /deep/ .el-card__body {
        overflow: auto;
        height: calc(100% - 42px);
      }
      /deep/ .el-card__header {
        position: relative;
      }
      svg {
        position: absolute;
        right: 10px;
        bottom: 12px;
        cursor: pointer;
      }
      .item-box {
        display: flex;
        align-items: center;
        padding: 7px 0;
        > div:nth-child(1) {
          margin: 0 12px;
        }
        > div:nth-child(2) {
          word-break: break-all;
          max-width: 55%;
        }
        /deep/ .signal-strength {
          position: absolute;
          right: 0;
        }
      }
    }
  }

  .crew-monitor-info {
    height: 100%;
    padding: 0 16px;
    /deep/ #crew-monitor {
      display: flex;
      flex-direction: column;
      height: 100%;
    }
  }
  > .el-col:nth-child(3) {
    display: flex;
    flex-direction: column;
    .map-box {
      height: 50%;
      margin: 0 0 12px 0;
      /deep/.el-card__body {
        // height: 452px;
        height: calc(100% - 41px);
        > div {
          width: 100%;
          height: 100%;
        }
      }
    }
    .curve-box {
      height: 50%;
      /deep/.el-card__body {
        height: calc(100% - 41px);
        // height: 452px;
      }
    }
  }
}
</style>



