<template>
  <div class="curve" :id="id"></div>
</template>

<script>
export default {
  name: 'Curve',
  props: ['id'],
  data() {
    return {
      eChart: null,
      eChartOpt: {}
    }
  },
  methods: {
    renderChart() {
      const data = []
      for (let i = 0; i < 100; i++) {
        const time = this.$moment().add(i, 'day').format('yyyy-MM-DD HH:mm:ss')
        const value = Number((Math.random() * (800 - 100) + 100).toFixed(0))
        const arr = [time, value]
        data.push(arr)
      }
      this.eChart || (this.eChart = this.$echarts.init(document.getElementById(this.id)))
      this.eChartOpt = {
        xAxis: {
          type: 'time',
          axisLine: {
            show: true,
            lineStyle: {
              color: '#9c9259'
            }
          },
          axisPointer: {
            lineStyle: {
              type: 'solid',
              color: '#9c9259',
              width: 2
            }
          }
        },
        yAxis: {
          type: 'value',
          axisLine: {
            show: true,
            lineStyle: {
              color: '#9c9259'
            }
          },
          axisTick: {
            show: true
          },
          splitLine: {
            lineStyle: {
              color: '#9c925977',
              type: 'dashed'
            }
          }
        },
        series: [
          {
            type: 'line',
            data: data,
            emphasis: {
              disable: true
            },
            lineStyle: {
              color: '#dd6b66'
            }
          }
        ],
        backgroundColor: '#333333',
        tooltip: {
          show: true,
          trigger: 'axis'
        },
        dataZoom: [
          //   {
          //     type: 'inside',
          //     minValueSpan: 1000 * 60 * 24
          //   },
          {
            type: 'slider',
            testStyle: {
              color: '#fff',
              fontSize: 25
            }
          }
        ]
      }
      this.eChart.setOption(this.eChartOpt)
    }
  },
  mounted() {
    this.renderChart()
  }
}
</script>

<style scoped lang="less">
.curve {
  width: 100%;
  height: 100%;
}
</style>



